  const sessionId = c.req.param('sessionId')
  const id = c.env.TASK_SESSION.idFromName(sessionId)
  const stub = c.env.TASK_SESSION.get(id)

  const url = new URL(c.req.url)
  url.pathname = '/broadcast'

